

function CareScale({ scaleValue, careType }) {
    // Dclare un tableau de valeurs de rfrence pour le niveau de soin (1, 2, 3)
    const range = [1, 2, 3];
    
    // Dtermine le type d'icne  afficher en fonction du type de soin : '' pour 'light', '' pour 'water'
    const scaleType = careType === 'light' ? '' : '';

    return (
        <div>
            {/* Utilise map() pour parcourir le tableau range et afficher l'icne en fonction du scaleValue */}
            {range.map((rangeElem) =>
                // Si scaleValue est suprieur ou gal  rangeElem, affiche l'icne scaleType
                scaleValue >= rangeElem ? 
                    <span key={rangeElem.toString()}>{scaleType}</span> 
                    : 
                    // Si scaleValue est infrieur, ne rien afficher (null)
                    null
            )}
        </div>
    );
}


export default CareScale