import { useState } from "react";
import Banner from "./Banner";
import Cart from "./Cart";
import ShoppingList from "./ShoppingList";
import Footer from "./Footer";

function App(){
  const [cartItems, setCartItems] = useState([]);
  const addToCart = (name, price) => {
    setCartItems((prevItems) => {//prevItems reprsente la version actuelle de cartItems (le panier avant lajout de la nouvelle plante).
      const existingItem = prevItems.find((item) => item.name === name);//find() parcourt prevItems pour vrifier s'il existe un article dont le name correspond  la plante ajoute. Si une plante avec le mme name est dj prsente dans prevItems, elle est stocke dans existingItem. Si la plante n'existe pas encore, existingItem sera undefined.
      if(existingItem){
        return prevItems.map((item) =>
        item.name === name ? {...item, quantity: item.quantity + 1} : item);//map() est utilis pour crer une nouvelle version de prevItems, o chaque lment est vrifi. Pour l'lment correspondant  la plante (cest--dire item.name === name), on cre un nouvel objet avec les mmes proprits (...item) mais avec quantity incrment de 1 (quantity: item.quantity + 1). Les autres lments du panier sont laisss inchangs (: item).
      }else{
        return [...prevItems, {name, price, quantity: 1}];//Si la plante nest pas dj dans le panier, elle est ajoute en tant que nouvel lment. ...prevItems ajoute tous les lments existants, puis { name, price, quantity: 1 } ajoute la nouvelle plante avec une quantit initiale de 1.
      }
    });
  };

  const clearCart = () => {
    setCartItems([]);
  };

  const clearElement = (name) => {
    setCartItems((prevItems) => {
      return prevItems.map((item) =>
        item.name === name ? {...item, quantity: item.quantity - 1} : item) // Dcrmente la quantit si la plante est trouve
        .filter((item) => item.quantity > 0); // Retire l'lment du tableau si sa quantit tombe  0
    });
  };

  return (
    <div>
      <Banner />
      <div className="main">
        <Cart cartItems={cartItems} clearCart={clearCart} clearElement={clearElement}/>
        <ShoppingList addToCart={addToCart}/>
      </div>
      <Footer />  
    </div> 
  );

}

export default App;
